/*  h2      section */
@mixin visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0,0,0,0);
  overflow: hidden;
  @content  /*      */; }

/*   : @include visually-hidden() */


/* px  rem  */
@function rem($size, $basicSize: 16px) {
  @return $size / $basicSize#{rem}; }
/* font-size: rem(24px);
   line-height: rem(28px); */


/* px  em  */
@function em($px, $base: 14px) {    //    
  @return ($px / $base) * 1em; }
/* font-size: em(24px);
   line-height: em(28px); */

